 .single-flashcard {
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  /* min-height: 250px; */
  position: relative;
  -webkit-transition: -webkit-transform 0.8s;
  transition: -webkit-transform 0.8s;
  transition: transform 0.8s;
  transition: transform 0.8s, -webkit-transform 0.8s;
  -webkit-transform-style: preserve-3d;
          transform-style: preserve-3d;
  border-radius: 10px;
}


.single-flashcard-front, .single-flashcard-back {
  position: absolute;
  -webkit-backface-visibility: hidden;
          backface-visibility: hidden;  
  border-radius: 10px;  
  text-align: center;
}

.single-flashcard-front {
  border: 2px solid #fbb13c;
  min-height: 250px;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.single-flashcard-back {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
  border: 2px solid #f36;
  min-height: 250px;
  width: 80vw;
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}

.card-flip-motion,
.card-flip-motion .single-flashcard-container {
  -webkit-transform: rotateY(180deg);
          transform: rotateY(180deg);
}

.single-flashcard-back .card-button-container,
.single-flashcard-front .card-button-container {
  display: flex;
  width: 80vw;
  justify-content: space-around;
}

